@import "./src/assets/themes/appdb/variables.scss";

.wrapper {
  width: 100%;

  &_absolute {
    width: auto;
    position: absolute;
    display: flex;
    align-items: center;
    background-color: var(--bg);
    left: 1px;
    right: 1px;
    top: 1px;
    bottom: 1px;
  }
}

.loader {
  border-radius: 50%;
  margin: 0 auto;
  position: relative;
  border: 6px solid $loader-secondary;
  border-left: 6px solid $primary;
  transform: translateZ(0);
  animation: load 1.1s infinite linear;
}

@keyframes load {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
